import React, { Component } from 'react';
// import ReactDOM from "react-dom";
import {Icon,Layout,Modal,Input } from 'antd';
import Event from "../../../assets/events.js";
import "../../../style/front/common.css";
import axios from 'axios';
const { Header } = Layout;

class RightTop extends Component {
    state = {
        collapsed: true,
        loginVisible: false,
        signVisible: false,
        logoutVisible: false,
        username:'',
        password:'',
        repassword:'',
        isLogin: window.localStorage.username ? true :false
    };
    componentDidMount(){
        this.isLogin();
    }
    toggle = () => {
        Event.emit("collapsed", this.state.collapsed);
        this.setState({
            collapsed: !this.state.collapsed,
        });
    }
    showModal = (type) => {
        if(type === 'login'){
            this.setState({
                loginVisible: true,
                username:'',
                password:'',
                repassword:''
            });
        }else{
            this.setState({
                signVisible: true,
                username:'',
                password:'',
                repassword:''
            });
        }
    }
    
    handleOk = (type,e) => {
        if(type === 'login'){
            this.setState({
                loginVisible: false,
            });

            alert(this.state.username);
            this.login();
        }else if(type === 'sign'){
            this.setState({
                signVisible: false,
            });
            this.sign();
        }else{
            var that = this;
            this.setState({
                logoutVisible: false
            })
            axios.get('http://localhost:8003/admin/user/loginOut')
            .then((res)=>{
                alert(res.data.message);
                window.localStorage.username = '';
                that.setState({
                    isLogin: false
                })
                that.login();
            }).then((err)=>{
                console.log(err);
            })
        }
    }
    
    handleCancel = (type,e) => {
        if(type === 'login'){
            this.setState({
                loginVisible: false,
            });
        }else if(type === 'sign'){
            this.setState({
                signVisible: false,
            });
        }else{
            this.setState({
                logoutVisible: false,
            });
        }
      }
      emitEmpty = () => {
        this.userNameInput.focus();
        this.setState({ username: '' });
      }
    
      onChangeUserName = (e) => {
        this.setState({ username: e.target.value });
      }
      onChangepassword = (e) => {
        this.setState({ password: e.target.value });
      }
      onChangeRepassword = (e) => {
        this.setState({ repassword: e.target.value });
      }
      isLogin = ()=>{
          if(this.state.isLogin){
            document.querySelector('.user').style.display = 'block';
            document.querySelector('.custom').style.display = 'none';
          }else{
            document.querySelector('.user').style.display = 'none';
            document.querySelector('.custom').style.display = 'block';
          }
      }
      login = () =>{
        var that = this;
        axios.post('http://localhost:8003/admin/sign_in', {
            username: that.state.username,
            password: that.state.password
          })
          .then(function (response) {
            alert(response.data.message);
            if(response.data.code === 4){
                that.setState({
                    isLogin: true
                })
                window.localStorage.username = response.data.userInfo.username;
            }
            that.isLogin();
          })
          .catch(function (error) {
            console.log(error);
          });
      }
      sign = () =>{
        var that = this;
        // var params = new FormData();
        // params.append('username',this.state.username);
        // params.append('password',this.state.password);
        // params.append('repassword',this.state.repassword);
        axios.post('http://localhost:8003/admin/register',{
            username: that.state.username,
            password: that.state.password,
            repassword: that.state.repassword
        })
        .then(function(data){
            alert(data.data.message);
        })
        .then(function(err){
            console.log(err);
        })
      }
      showlogout = () =>{
        this.setState({
            logoutVisible: !this.state.logoutVisible
        })
      }
    render() {
        const { password,repassword} = this.state;
        const username = window.localStorage.username;
        const suffixusername = username ? <Icon type="close-circle" onClick={this.emitEmpty} /> : null;
        const suffixpsd = password ? <Icon type="eye" onClick={this.emitEmpty} /> : null;
        return (
            <div>
                <Header style={{ background: '#fff', padding: 0 }}>
                    <Icon
                        className="trigger"
                        type={this.state.collapsed ? 'menu-unfold' : 'menu-fold'}
                        onClick={this.toggle}
                    />
                    <span className='login custom'>
                        <span onClick={this.showModal.bind(this,'login')}>登陆</span>
                        <span onClick={this.showModal.bind(this,'sign')}>注册</span>
                    </span>
                    <span className='login user'>
                        <span><Icon type='user'></Icon></span>
                        <span onClick={this.showlogout}>{username}</span>
                    </span>
                    <Modal 
                        title="exit login"
                        visible={this.state.logoutVisible}
                        onOk={this.handleOk.bind(this,'logout')}
                        onCancel={this.handleCancel.bind(this,'logout')}
                    >
                        退出当前帐号？
                    </Modal>
                    <Modal
                        title="Login Modal"
                        visible={this.state.loginVisible}
                        onOk={this.handleOk.bind(this,'login')}
                        onCancel={this.handleCancel.bind(this,'login')}
                        >
                        <p>      
                            <Input
                                placeholder="Enter your username"
                                prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                suffix={suffixusername}
                                value={username}
                                onChange={this.onChangeUserName}
                                ref={node => this.userNameInput = node}
                            />
                        </p>
                        <p>
                            <Input
                                    placeholder="Enter your password"
                                    type='password'
                                    prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                    suffix={suffixpsd}
                                    value={password}
                                    onChange={this.onChangepassword}
                                    // ref={node => this.userNameInput = node}
                                />
                        </p>
                    </Modal>
                    <Modal
                        title="sign in Modal"
                        visible={this.state.signVisible}
                        onOk={this.handleOk.bind(this,'sign')}
                        onCancel={this.handleCancel.bind(this,'sign')}
                        >
                        <p>      
                            <Input
                                placeholder="Enter your username"
                                prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                suffix={suffixusername}
                                value={username}
                                onChange={this.onChangeUserName}
                                ref={node => this.userNameInput = node}
                            />
                        </p>
                        <p>
                            <Input
                                    placeholder="Enter your password"
                                    type='password'
                                    prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                    suffix={suffixpsd}
                                    value={password}
                                    onChange={this.onChangepassword}
                                    // ref={node => this.userNameInput = node}
                                />
                        </p>
                        <p>
                            <Input
                                    placeholder="Enter your repassword"
                                    type='password'
                                    prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                    suffix={suffixpsd}
                                    value={repassword}
                                    onChange={this.onChangeRepassword}
                                    // ref={node => this.userNameInput = node}
                                />
                        </p>
                    </Modal>
                </Header>

            </div>
        )
    }
}
export default RightTop;

