import React, {Component} from 'react';
import {Breadcrumb} from 'antd';
import Event from '../../../assets/events';
class Brands extends Component{
    state = {
        firstBrand: '',
        secondBrand:''
    }
    componentDidMount(){
        Event.on('brands',(msg) =>{
            this.setState({
                firstBrand: msg[0],
                secondBrand: msg[1]
            });
            this.render();
        })
    }
    render(){
        return (
            <div>
                <Breadcrumb style={{ margin: '16px 0' }}>
                    <Breadcrumb.Item>后台管理</Breadcrumb.Item>
                    <Breadcrumb.Item>{this.state.firstBrand}</Breadcrumb.Item>
                    <Breadcrumb.Item>{this.state.secondBrand}</Breadcrumb.Item>
                </Breadcrumb>
            </div>
        );
    }
}

export default Brands;