import React, { Component } from 'react';
import { Menu, Icon, Layout } from 'antd';
import "../../../style/front/common.css";
import Event from '../../../assets/events';
const SubMenu = Menu.SubMenu;
const {Sider} = Layout;
class SideBar extends Component {
    state = {
        theme: 'dark',
        current: '1',
      }
    changeTheme = (value) => {
        this.setState({
            theme: value ? 'dark' : 'light',
        });
    }
    handleClick = (e) => {
        console.log('click ', e);
        var brands = [];
        var firstbrand = e.keyPath[1];
        var secondbrand= e.item.props.children;
        brands.push(firstbrand);
        brands.push(secondbrand);
        this.setState({
            current: e.key,
        });
        Event.emit('brands',brands);
    }
    toManageBlogs =  () => {
        this.$router.push('./manageBlogs');
    }
    changeBrand = () =>{
        console.log();
    }
    render() {
      return (
         <div className="SideBar">
            <Sider width={200} style={{ background: '#fff' }}>
                <Menu
                    onClick={this.handleClick}
                    mode="inline"
                    defaultSelectedKeys={[this.state.current]}
                    defaultOpenKeys={['用户管理']}
                    style={{ height: '100%', borderRight: 0 }}
                >
                    <SubMenu key="用户管理" title={<span><Icon type="user" />用户管理</span>}>
                        <Menu.Item key="1">用户列表</Menu.Item>
                        <Menu.Item key="2">消息</Menu.Item>
                        <Menu.Item key="3">权限管理</Menu.Item>
                        {/* <Menu.Item key="4">option4</Menu.Item>  */}
                    </SubMenu>
                    <SubMenu key="博客管理" title={<span><Icon type="laptop" />博客管理</span>}>
                        <Menu.Item key="5">博客列表</Menu.Item>
                        <Menu.Item key="6">博客管理</Menu.Item>
                        <Menu.Item key="7">添加博客</Menu.Item>
                        {/* <Menu.Item key="8"></Menu.Item> */}
                    </SubMenu>
                    <SubMenu key="草稿" title={<span><Icon type="notification" />草稿</span>}>
                        <Menu.Item key="9">草稿列表</Menu.Item>
                        <Menu.Item key="10">分类管理</Menu.Item>
                    </SubMenu>
                </Menu>
            </Sider>
      </div>
      );
  }
}

export default SideBar;
