import React from 'react';
import {
   Layout
} from 'antd';
import LeftBar from "../../components/common/protal/leftbar/index";
import axios from 'axios';
const {Content} = Layout;
class Page extends React.Component {
  state = {
    fileLists: ['<p>friend</p>','d']
  }
  componentDidMount(){
    this.getPages();
    // this.forceUpdate();
  }
  getPages = () =>{
    var that = this;
    axios.get('http://localhost:8003/front/readfile')
          .then((res) =>{
            that.toDOM(res.data);
          })
          .catch(err =>{
            console.log(err);
          });
  }
  toDOM = (arg) =>{
      var files = arg || [];
      var ul = document.querySelector('.fileWrapper');
      console.log(ul);
      files.forEach( item => {
        var objE = document.createElement("li");
        objE.innerHTML = item;
        console.log(objE);
        ul.append(objE);
      })
  }
  render() {
      return (
        <Layout>
          <LeftBar></LeftBar>
          <Layout>
            <Content style={{
              margin: '24px 16px', padding: 24, background: '#fff', minHeight: 280,
            }}
            >
              <ul className= 'fileWrapper'>
              
              </ul>
            </Content>
          </Layout>
        </Layout>
      );
  }
}

export default Page;